import React, { useEffect, useState } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'
import Card from './Card/Card'
import { imagesCareerPro } from '../imagesCareerPro'
import Slider from 'react-slick'
import { setLocalData, removeLocalData } from 'Shared/helpers/session'
import { withRouter } from 'react-router'
import useApiGetPackages from '../../../../services/api/useApiGetPackages'
import './PricingCard.scss'
import { ColLoader } from 'Shared/helpers/skeletonLoader'

const PricingCard = (props) => {
  const { t } = props
  const ICONS = {...imagesCareerPro}
  const [packages, setPackages] = useState()

  const { data, apiGetPackages } = useApiGetPackages()

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1500,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true
        }
      },
      {
        breakpoint: 764,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
          dots: true,
          arrows: true,
          autoplay: false
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: true,
          arrows: true,
          autoplay: false
        }
      }
    ]
  }

  const onSelectPackage = (pkgId) => {
    setLocalData('selected_package', pkgId)
    props.history.push('/career-pro/checkout')
  }

  useEffect(() => {
    apiGetPackages()
    removeLocalData('promocode')
    removeLocalData('promocode_result')
  }, [])

  useEffect(() => setPackages(data), [data])

  return (
    <Container className='careerpro__section pricing__section--careerpro'>
      <h2 className='title'>{t('careerProPage.planPage.sections.pricing.heading')}</h2>
      { !packages
        ? <ColLoader cols={3} height={550} />
        : <Slider {...settings}>
          { packages && packages.map(p => { return <div key={p.id}> <Card icons={ICONS} content={p} onSelectPackage={onSelectPackage} /> </div> }) }
        </Slider>
      }
    </Container>
  )
}

export default withRouter(withTranslations(PricingCard))



