import React from 'react'
import './Modal.scss'
import ModalHeader from '../../../../components/Modal/components/Header'
import ModalFooter from '../../../../components/Modal/components/Footer'
import ReactModal from 'react-modal'
import classnames from 'classnames'
import { isArabicLayout } from 'Shared/helpers/layout'
import { imagesCareerPro } from 'Shared/layouts/CareerPro/Components/imagesCareerPro'
import HtmlParser from 'react-html-parser'

const Modal = (props) => {
  const {
    children,
    modalIsOpen,
    afterOpenModal,
    closeModal,
    wide,
    title,
    className,
    showSubmit,
    showCancel,
    loading,
    footerClassName,
    disableCancelButton,
    cancelButtonClassName,
    disableSubmitButton,
    submitButtonClassName,
    handleSubmit,
    submitText,
    cancelText,
    heading,
    des,
    status,
    btnFullW,
    outsideClickClose,
    hideCloseBtn = true,
    textCenter
  } = props

  const ICONS = {...imagesCareerPro}
  const modalClass = classnames({modal: true, 'modal--wide': wide, [className]: !!className})
  const portalClass = classnames({ 'modal__portal': true, 'main--arabic': isArabicLayout })

  return (
    <ReactModal
      portalClassName={portalClass}
      overlayClassName='modal__overlay'
      bodyOpenClassName='modal-opened'
      className={`${modalClass} ${hideCloseBtn === true && 'custom_modal'} ${status == 'careerpro' && 'careerpro_modal'}`}
      isOpen={modalIsOpen}
      onAfterOpen={afterOpenModal}
      onRequestClose={closeModal}
      contentLabel='Example Modal'
      shouldCloseOnOverlayClick={outsideClickClose}
    >
      <ModalHeader handleClose={closeModal} title={title} />
      <div className={`modal__body ${textCenter && `text-center`}`}>
            
        <div className={`img_icon `}>
          <img src={ICONS[status]} className={`${status == 'careerpro' && 'careerpro_img'}`} alt='icon' />
        </div>
          
        <h2 className='heading'>{HtmlParser(heading)}</h2>
        <p className='des'>{HtmlParser(des)}</p>
      </div>
      {(showSubmit || showCancel) && (<ModalFooter
        disableCancelButton={disableCancelButton}
        cancelButtonClassName={cancelButtonClassName}
        loading={loading}
        handleCancel={closeModal}
        disableSubmitButton={disableSubmitButton}
        submitButtonClassName={submitButtonClassName}
        handleSubmit={handleSubmit}
        showSubmit={showSubmit}
        showCancel={showCancel}
        submitText={submitText}
        cancelText={cancelText}
        shouldCloseOnOverlayClick={false}
        footerClassName={footerClassName}
        btnFullW={btnFullW}
      />)}
        
    </ReactModal>
  )
}

export default Modal



