import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import how_works from 'Shared/assets/images/illustrations/howWorksImg.svg'
import Container from 'Shared/components/Container'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import './HowWorks.scss'

const HowWorks = ({ t }) => (

  <section className='howWorks__section'>
    <Container>
      <div className='columns howWorksCol'>
        <div className='column col-4 col-xs-12'>
          <div>
            <h2 className='title'>{t('careerProPage.planPage.sections.howWorks.heading')}</h2>

            <div className='list__howWorks'>
              <ul>
                <li className='list__heading highlight__list'>{t('careerProPage.planPage.sections.howWorks.list.qus1')}</li>
                <li className='list__des'>{t('careerProPage.planPage.sections.howWorks.list.ans1')}</li>
              </ul>

              <ul>
                <li className='list__heading'>{t('careerProPage.planPage.sections.howWorks.list.qus2')}</li>
                <li className='list__des'>{t('careerProPage.planPage.sections.howWorks.list.ans2')}</li>
              </ul>

              <ul>
                <li className='list__heading'>{t('careerProPage.planPage.sections.howWorks.list.qus3')}</li>
                <li className='list__des'>{t('careerProPage.planPage.sections.howWorks.list.ans3')}</li>
              </ul>

              <ul>
                <li className='list__heading'>{t('careerProPage.planPage.sections.howWorks.list.qus4')}</li>
                <li className='list__des'>{t('careerProPage.planPage.sections.howWorks.list.ans4')}</li>
              </ul>

              <ul>
                <li className='list__heading'>{t('careerProPage.planPage.sections.howWorks.list.qus5')}</li>
                <li className='list__des'>{t('careerProPage.planPage.sections.howWorks.list.ans5')}</li>
              </ul>

              <ul>
                <li className='list__heading'>{t('careerProPage.planPage.sections.howWorks.list.qus6')}</li>
                <li className='list__des'>{t('careerProPage.planPage.sections.howWorks.list.ans6')}</li>
              </ul>

              <ul>
                <li className='list__heading'>{t('careerProPage.planPage.sections.howWorks.list.qus7')}</li>
                <li className='list__des'>{t('careerProPage.planPage.sections.howWorks.list.ans7')}</li>
              </ul>
            </div>

          </div>
        </div>
        <div className='column col-8 col-xs-12 text-center'>
          <img src={how_works} className='howWorks__img' />
        </div>
      </div>
    </Container>
  </section>
)

export default withTranslations(HowWorks)



