import { useState } from "react";
import { getRequest } from "./commonApi";
import { endPoints } from "./endPoints";
import { setLocalData } from "Shared/helpers/session";

const useApiGetPackages = () => {
  
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState(null);
  
  const apiGetPackages = async () => {

    const url = endPoints.getPackages
  
    try {

      let res = await getRequest(url);
 
      if(res.success) {
        setData(res.data || res)
        setLocalData('career_pro_packages', res.data)
        return res.data;
      }

    } catch(err) {
      console.log("Err", err);
    }

  };

  return { loading, data, apiGetPackages };

};

export default useApiGetPackages;


