import React, { useEffect } from "react"
import './Card.scss'
import Col from "../../Col"
import mixpanel from 'mixpanel-browser'

export default function Card (props) {
  const { icons, content, onSelectPackage } = props

    let desc = content.description && content.description.split("|");
        // desc = [...desc];
    let features = content.features && content.features.split("|");
        // features = [...features];

    return (
                <div className="careerPro__pricingCard--main">

                    <div className="careerPro__pricingCard--inner">
                    {content.featured && <p className="best_value">{content.featured}</p>}
                    <div className="package__heading">
                        <h2>{content.name}</h2>
                        <div className="package__heading-span">
                            {content.old_price && <span className="package__heading--span_cutPrice">{`$${content.old_price}`}</span>}
                            {content.off_sale && <span className="package__heading--span_off">{content.off_sale}</span>}
                        </div>
                    </div>

                    <div className={`${content.name == 'Basic' || content.name == 'Premium' ? 'package__title__spacing' : ''} package__title`}>
                        <ul className="featured_list">
                            {desc.map((feature, i) => (
                                <li key={i}>{feature}</li>
                            ))}
                        </ul>
                        <div className="package__title--span">
                            <span className="package__title--price">${content.price}</span>
                            {content.vat && <span className="package__title--vat">{content.vat}</span>}
                        </div>
                    </div>

                    <button className="price__btn" onClick={()=> {onSelectPackage(content); mixpanel.track(`Career Pro - Package Select`); mixpanel.track(`Career Pro - Package Select - ${content.name}`); }}>Select</button>

                    <div className="package__des--list">
                        <ul className={`${content.name == 'Standard' || content.name == 'Premium' ? 'font__style_list' : ''}`}>
                            { features && features.map((des, i) => (
                                <li key={i}><img src={icons.blue_tick} /> {des}</li>
                            ))}
                        </ul>
                    </div>
                    </div>

                </div>
    )
}


