import React from 'react';
import withTranslations from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'
import Slider from 'react-slick'
import './Testimonials.scss'
import client1 from 'Shared/assets/images/other/client1.svg';
import { imagesCareerPro } from '../imagesCareerPro';

function SampleNextArrow(props) {
    const ICONS = {...imagesCareerPro}
    const { onClick } = props;
    return (
      <div className={`careerpro-slider-arrow` } onClick={onClick}>
          <img src={ICONS.slider_right_arrow} />
      </div>
    );
  }
  
  function SamplePrevArrow(props) {
    const ICONS = {...imagesCareerPro}
    const { onClick } = props;
    return (
      <div className={`careerpro-slider-arrow blockStyle`} onClick={onClick}>
        <img src={ICONS.slider_left_arrow} />
      </div>
    );
  }

const Testimonials = ({ t }) => {

    const ICONS = {...imagesCareerPro}

    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        responsive: [
          {
            breakpoint: 1500,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
            },
          },
          {
            breakpoint: 764,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              initialSlide: 1,
              dots: true,
              arrows: true,
              autoplay: false,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              dots: true,
              arrows: false,
              autoplay: false,
            },
          },
        ],
      }

      const reviews = [
        { id: 1, name: t('careerProPage.planPage.sections.reviews.client1.name'), designation: t('careerProPage.planPage.sections.reviews.client1.designation'), user_img: client1, quote_img: ICONS.slider__quote, description: t('careerProPage.planPage.sections.reviews.client1.paragraph')},
        // { id: 2, name: t('careerProPage.planPage.sections.reviews.client1.name'), designation: t('careerProPage.planPage.sections.reviews.client1.designation'), user_img: client1, quote_img: ICONS.slider__quote, description: t('careerProPage.planPage.sections.reviews.client1.paragraph')},
        // { id: 3, name: t('careerProPage.planPage.sections.reviews.client1.name'), designation: t('careerProPage.planPage.sections.reviews.client1.designation'), user_img: client1, quote_img: ICONS.slider__quote, description: t('careerProPage.planPage.sections.reviews.client1.paragraph')},
      ]


    return (
        <section className='careerpro__section_testimonial'>
            <Container>
                <Slider {...settings} className='slick__slider_careerpro'>

                { reviews.map( (item, index) => { 
                  return (
                    <div key={index} className='careerPro__testimonial'>
                        <img src={item.quote_img} />
                        <p>{item.description}</p>
                        <div className='careerPro__testimonial--inner'>
                            <img src={item.user_img} />
                            <h3>{item.name}</h3>
                            <h4>{item.designation}</h4>
                        </div>
                    </div>
                  ) 
                } ) }

                </Slider>
            </Container>
        </section>
    )
}

export default withTranslations(Testimonials)



