import React from "react";
import withTranslations from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'
import Collapse, { Panel } from 'rc-collapse'
import ReactHtmlParser from 'react-html-parser'
import AccordionPanelHeader from 'Shared/components/AccordionPanel/AccordionPanelHeader'
import Row from "../Row";
import Col from "../Col";
import './Faqs.scss'

const Faqs = ({t}) => {

 
    return (
        <Container className="careerpro__section faq_section__careerpro">
            <h2 className="title">{t('careerProPage.planPage.sections.faqs.heading')}</h2>

            <Collapse className='faq__collapse-container'>
                <Panel showArrow={false} header={<AccordionPanelHeader>{t('careerProPage.planPage.sections.faqs.faqQuestions.qus1')}</AccordionPanelHeader>}>{ReactHtmlParser(t('careerProPage.planPage.sections.faqs.faqQuestions.ans1'))}</Panel>
            </Collapse>

            <Collapse className='faq__collapse-container'>
                <Panel showArrow={false} header={<AccordionPanelHeader>{t('careerProPage.planPage.sections.faqs.faqQuestions.qus2')}</AccordionPanelHeader>}>{ReactHtmlParser(t('careerProPage.planPage.sections.faqs.faqQuestions.ans2'))}</Panel>
            </Collapse>

            <Collapse className='faq__collapse-container'>
                <Panel showArrow={false} header={<AccordionPanelHeader>{t('careerProPage.planPage.sections.faqs.faqQuestions.qus3')}</AccordionPanelHeader>}>{ReactHtmlParser(t('careerProPage.planPage.sections.faqs.faqQuestions.ans3'))}</Panel>
            </Collapse>

            <Collapse className='faq__collapse-container'>
                <Panel showArrow={false} header={<AccordionPanelHeader>{t('careerProPage.planPage.sections.faqs.faqQuestions.qus4')}</AccordionPanelHeader>}>{ReactHtmlParser(t('careerProPage.planPage.sections.faqs.faqQuestions.ans4'))}</Panel>
            </Collapse>

            <Collapse className='faq__collapse-container'>
                <Panel showArrow={false} header={<AccordionPanelHeader>{t('careerProPage.planPage.sections.faqs.faqQuestions.qus5')}</AccordionPanelHeader>}>{ReactHtmlParser(t('careerProPage.planPage.sections.faqs.faqQuestions.ans5'))}</Panel>
            </Collapse>

            <Collapse className='faq__collapse-container'>
                <Panel showArrow={false} header={<AccordionPanelHeader>{t('careerProPage.planPage.sections.faqs.faqQuestions.qus6')}</AccordionPanelHeader>}>{ReactHtmlParser(t('careerProPage.planPage.sections.faqs.faqQuestions.ans6'))}</Panel>
            </Collapse>

            <Collapse className='faq__collapse-container'>
                <Panel showArrow={false} header={<AccordionPanelHeader>{t('careerProPage.planPage.sections.faqs.faqQuestions.qus7')}</AccordionPanelHeader>}>{ReactHtmlParser(t('careerProPage.planPage.sections.faqs.faqQuestions.ans7'))}</Panel>
            </Collapse>


        </Container>
    )
}

export default withTranslations(Faqs)


