export const MODAL_DATA = {
    "success_login_user": {
        "submitText":"Update Profile",
        "cancelText":"Continue",
        "handleSubmit": ()=> window.location = "/candidate/profile/preview",
        "showSubmit":true,
        "showCancel":true,
        "status":'success',
        "heading":"Congratulations! We're thrilled to be part of your journey towards career success.",
        "des":"To ensure we craft the most impactful and personalized documents for you, please update your profile.",
    },
    "success_nonlogin_user": {
        "submitText":"Sign Up",
        "cancelText":"Continue",
        "showSubmit":true,
        "handleSubmit": ()=> window.location = "/candidate/sign-up",
        "showCancel":true,
        "status":'success',
        "heading":"Congratulations! We're thrilled to be part of your journey towards career success.",
        "des":"To ensure we craft the most impactful and personalized documents for you, please sign in and complete your profile.",
    },
    "failed": {
        "submitText":"Retry",
        "cancelText":"Cancel",
        "handleSubmit": ()=> window.location = "/career-pro/",
        "showSubmit":true,
        "showCancel":true,
        "status":'fail',
        "heading":"Oops! Payment Failed.",
        "des":"It seems that there was an issue processing your payment. Please double-check your payment details and try again. If the problem persists or you need any assistance, feel free to reach out to our support team.",
    }
}


export const PAYMENT_STATUS_TYPE = {
    'succeeded': 'success',
    'failed': 'fail'
}


