import React, { Component } from 'react'
import { Helmet } from 'react-helmet'
import ReactPlayer from 'Shared/components/AsyncReactPlayer'
import LazyLoad from 'react-lazyload'
import withTranslation from 'Shared/hoc/withTranslations'
import { Trans } from 'react-i18next'
import Container from 'Shared/components/Container'
import { PrimaryButtonOutline, PrimaryButton } from 'Shared/components/Button'
import { aboutPage } from 'Shared/constants/MetaTags'
import offsets from 'Shared/constants/LazyLoad'
import { CandidateRoutes, StaticRoutes } from 'Shared/constants/Routes'

import OlivTeamImage from 'Shared/assets/images/brand/oliv-team.jpg'
import VideoThumbnail from 'Shared/assets/images/brand/video-thumbnail.png'
import samsungLogo from 'Shared/assets/images/logos/samsung-logo-about-us.svg'
import sephoraLogo from 'Shared/assets/images/logos/sephora-logo-about-us.svg'
import canonLogo from 'Shared/assets/images/logos/canon-logo-about-us.svg'
import omnicomLogo from 'Shared/assets/images/logos/omnicom-logo-about-us.svg'
import boseLogo from 'Shared/assets/images/logos/bose-logo-about-us.svg'

import WebpHeadingImage from 'Shared/assets/webp/candidate_hero.webp'
import Jpeg2000HeadingImage from 'Shared/assets/jpeg2000/candidate_hero.jpf'
import JpegxrHeadingImage from 'Shared/assets/jpegxr/candidate_hero.wdp'
import HeadingImage from 'Shared/assets/images/illustrations/candidate_hero.png'

import './AboutUs.scss'

const AboutUsLogos = [
  {
    src: samsungLogo,
    key: 'Samsung'
  },
  {
    src: sephoraLogo,
    key: 'Sephora'
  },
  {
    src: canonLogo,
    key: 'Canon'
  },
  {
    src: omnicomLogo,
    key: 'Omnicom'
  },
  {
    src: boseLogo,
    key: 'Bose'
  }
]

class AboutUs extends Component {
  state = {
    videoPlayerOpen: false
  }

  openVideoPlayer = () => this.setState({ videoPlayerOpen: true })
  closeVideoPlayer = () => this.setState({ videoPlayerOpen: false })

  redirectToCandidateSignup = () => this.props.history.push(CandidateRoutes.signUp + '?utm_source=about')
  redirectToEmployersLP = () => this.props.history.push(StaticRoutes.employers + '?utm_source=about')

  getClientsLogos = () => AboutUsLogos
    .map((logo) => (
      <span className='c-company-logos__logo' key={logo.key}>
        <LazyLoad offset={offsets.small}>
          <img className='c-company-logos__logo__image' src={logo.src} />
        </LazyLoad>
      </span>)
    )

  render () {
    const { t } = (this.props)
    return (
      <section className='about-us-page__container parsing--hidden'>
        <Helmet>
          <title>{aboutPage.title}</title>
          <meta name='description' content={aboutPage.description} />
          <meta property='og:title' content={aboutPage.title} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={aboutPage.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={aboutPage.url} />
          <meta property='og:description' content={aboutPage.description} />
        </Helmet>
        <Container>
          <div className='about-us__container about-us__container-speech'>
            <h1 className='about-us__header'>{t('aboutPage.mainTitle')}</h1>
            <Trans i18nKey='aboutPage.mainDescription'>
              <p className='about-us__paragraph about-us__paragraph--main'>
                Launched in 2013, we set out to tackle a huge problem -  <a target='_blank' href='https://oliv.com/employers/blog/youth-unemployment'>a 30% youth unemployment rate in the MENA region.</a> Oliv helps university students and graduates launch their careers. And we're pretty good at it.
              </p>
              <p className='about-us__paragraph about-us__paragraph--main'>
                Today, it's home to thousands of students and graduates where their ambitions are aligned to opportunity. More than 1,000 businesses from startups to Fortune 500s use Oliv to hire top emerging talent to maintain a healthy talent pipeline and grow their business for the future.
              </p>
              <p className='about-us__paragraph about-us__paragraph--last'>
                Our goal is to make launching careers simple, personal and rewarding. That's Oliv.
              </p>
            </Trans>
          </div>
        </Container>
        <div className='about-us__container about-us__container-team'>
          <LazyLoad offset={offsets.small}>
            <img src={OlivTeamImage} alt='oliv-team' className='about-us__team-image' />
          </LazyLoad>
        </div>
        <Container className='about-us__container about-us__container-video'>
          <LazyLoad offset={offsets.small}>
            <img src={VideoThumbnail} alt='oliv-video' className='about-us__image--video-trigger' onClick={this.openVideoPlayer} />
          </LazyLoad>
          <div className='columns about-us__counter--wrapper'>
            <div className='column col-lg-4 col-md-12 about-us__counter'>
              <div className='about-us__counter-container'>
                <h1 className='about-us__counter-amount'>+150k</h1>
                <p className='about-us__counter-title'>{t('aboutPage.counters.students')}</p>
              </div>
            </div>
            <div className='column col-lg-4 col-md-12 about-us__counter'>
              <div className='about-us__counter-container'>
                <h1 className='about-us__counter-amount'>+1,000</h1>
                <p className='about-us__counter-title'>{t('aboutPage.counters.businesses')}</p>
              </div>
            </div>
            <div className='column col-lg-4 col-md-12 about-us__counter'>
              <div className='about-us__counter-container'>
                <h1 className='about-us__counter-amount'>+1,000</h1>
                <p className='about-us__counter-title'>{t('aboutPage.counters.interviewed')}</p>
              </div>
            </div>
          </div>
        </Container>
        <div className='about-us__divider' />
        {this.state.videoPlayerOpen &&
        <div onClick={this.closeVideoPlayer} className='about-us__video-palyer'>
          <div className='about-us__video-palyer-container'>
            <ReactPlayer
              url='https://www.youtube.com/watch?v=_zGySq6A_ts'
              width='100%'
              controls
              playing
              height='100%'
              className='react-player'
            />
          </div>
        </div>
        }
        <Container className='about-us__container about-us__container-press-highlights'>
          <div className='columns'>
            <div className='column col-4 col-md-12 about-us__press-highlights'>
              <h1>{t('aboutPage.pressHighlights.title')}</h1>
              <p className='about-us__paragraph'>{t('aboutPage.pressHighlights.description')}</p>
              <PrimaryButtonOutline small link href='https://press.oliv.com/' target='_blank'>
                {t('aboutPage.pressHighlights.cta')}
              </PrimaryButtonOutline>
            </div>
            <div className='column col-8 col-md-12'>
              <a className='about-us__press-link' href='https://www.forbesmiddleeast.com/en/u-a-e-innovation-fund-backs-dubai-startup-internsme/' target='_blank'>
                <span className='about-us__press-link-content about-us__press-link-title'>FORBES</span>
                <span className='about-us__press-link-content'>{t('aboutPage.pressHighlights.articleOne')}</span>
              </a>
              <a className='about-us__press-link' href='https://www.entrepreneur.com/article/311677' target='_blank'>
                <span className='about-us__press-link-content about-us__press-link-title'>ENTREPRENEUR</span>
                <span className='about-us__press-link-content'>{t('aboutPage.pressHighlights.articleTwo')}</span>
              </a>
              <a className='about-us__press-link' href='https://www.entrepreneur.com/article/285271' target='_blank'>
                <span className='about-us__press-link-content about-us__press-link-title'>ENTREPRENEUR</span>
                <span className='about-us__press-link-content'>{t('aboutPage.pressHighlights.articleThree')}</span>
              </a>
            </div>
          </div>
        </Container>
        <div className='about-us__divider' />
        <Container className='about-us__container about-us__container-companies'>
          <div className='columns'>
            <div className='column col-10 col-sm-12 about-us__companies-description'>
              <h1 className='about-us__header about-us__header--subheader'>{t('aboutPage.companies.title')}</h1>
              <p className='about-us__paragraph'>{t('aboutPage.companies.description')}</p>
            </div>
            <div className='column col-2 col-sm-12 about-us__companies-customer-stories-cta'>
              <PrimaryButtonOutline small link href='https://employers.oliv.com/customers' target='_blank'>{t('aboutPage.companies.cta')}</PrimaryButtonOutline>
            </div>
            <div className='column col-12 about-us__company-logos--container'>
              <div className='c-company-logos'>{this.getClientsLogos()}</div>
            </div>
          </div>
        </Container>
        <div className='about-us__divider' />
        <Container className='about-us__container about-us__container-headquarter'>
          <div className='columns'>
            <div className='column col-6 col-sm-12 about-us__headquarter-image'>
              <LazyLoad offset={offsets.small}>
                <picture>
                  <source srcSet={WebpHeadingImage} type='image/webp' />
                  <source srcSet={Jpeg2000HeadingImage} type='image/jp2' />
                  <source srcSet={JpegxrHeadingImage} type='image/wdp' />
                  <img className='candidates-intro__image' src={HeadingImage} alt='oliv candidates image' />
                </picture>
              </LazyLoad>
            </div>
            <div className='column col-6 col-sm-12 about-us__headquarter-description'>
              <h1 className='about-us__header about-us__header--subheader'>{t('aboutPage.headquarters.title')}</h1>
              <p className='about-us__paragraph'>{t('aboutPage.headquarters.description')}</p>
              <PrimaryButtonOutline className='about-us_container-headquarter-cta' small link href='https://careers.oliv.com' target='_blank'>
                {t('aboutPage.headquarters.cta')}
              </PrimaryButtonOutline>
            </div>
          </div>
        </Container>
        <div className='about-us__container about-us__container-ctas'>
          <Container>
            <div className='columns '>
              <div className='column col-6 col-sm-12'>
                <p className='about-us__paragraph white'>{t('aboutPage.insideCtaButtons.titleCandidates')}</p>
                <h1 className='about-us__header about-us__header--subheader white'>{t('aboutPage.insideCtaButtons.descriptionCandidates')}</h1>
                <PrimaryButton onClick={this.redirectToCandidateSignup}>{t('aboutPage.insideCtaButtons.getStarted')} &rarr;</PrimaryButton>
              </div>
              <div className='column col-6 col-sm-12 about-us__ctas--second-cta'>
                <p className='about-us__paragraph white'>{t('aboutPage.insideCtaButtons.titleEmployers')}</p>
                <h1 className='about-us__header about-us__header--subheader white'>{t('aboutPage.insideCtaButtons.descriptionEmployers')}</h1>
                <PrimaryButton onClick={this.redirectToEmployersLP}>{t('aboutPage.insideCtaButtons.learnMore')} &rarr;</PrimaryButton>
              </div>
            </div>
          </Container>
        </div>
      </section>
    )
  }
}

export default withTranslation(AboutUs)



