import React from 'react'
import Slider from 'react-slick'
import Container from 'Shared/components/Container'
import withTranslations from 'Shared/hoc/withTranslations'
import sample1 from 'Shared/assets/images/other/sample1.svg'
import sample2 from 'Shared/assets/images/other/sample2.svg'
import sample3 from 'Shared/assets/images/other/sample3.svg'
import './SampleWork.scss'

const SampleWork = ({t}) => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1500,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
        },
      },
      {
        breakpoint: 764,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
          dots: true,
          arrows: true,
          autoplay: false,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: true,
          arrows: false,
          autoplay: false,
        },
      },
    ],
  }
  return (
    <section className='careerpro__section sampleWork'>
      <Container>
        <h2 className='title'>{t('careerProPage.planPage.sections.sampleWork.heading')}</h2>
        <Slider {...settings} className='slick__slider_samplework'>
          <div className='sampleWorkBlock'>
            <img src={sample1} />
          </div>
          <div className='sampleWorkBlock'>
            <img src={sample2} />
          </div>
          <div className='sampleWorkBlock'>
            <img src={sample3} />
          </div>
        </Slider>
      </Container>
    </section>
  )
}

export default withTranslations(SampleWork)



