import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'
import Row from '../Row'
import Col from '../Col'
import './CareerProBenefits.scss'
import benefits1 from 'Shared/assets/images/other/benefits1.svg'
import benefits2 from 'Shared/assets/images/other/benefits2.svg'
import benefits3 from 'Shared/assets/images/other/benefits3.svg'
import benefits4 from 'Shared/assets/images/other/benefits4.svg'
import benefits5 from 'Shared/assets/images/other/benefits5.svg'
import benefits6 from 'Shared/assets/images/other/benefits6.svg'

const CareerProBenefits = ({t}) => {
  return (
    <Container className='careerpro__section'>
      <h2 className='title'>{t('careerProPage.planPage.sections.benefits.heading')}</h2>
      <Row>
        <Col col={4} xs={12} sm={12} md={4}>
          <div className='benefits__card'>
            <img src={benefits1} />
            <h2>{t('careerProPage.planPage.sections.benefits.list1.title')}</h2>
            <p>{t('careerProPage.planPage.sections.benefits.list1.des')}</p>
          </div>
        </Col>

        <Col col={4} xs={12} sm={12} md={4}>
          <div className='benefits__card'>
            <img src={benefits2} />
            <h2>{t('careerProPage.planPage.sections.benefits.list2.title')}</h2>
            <p>{t('careerProPage.planPage.sections.benefits.list2.des')}</p>
          </div>
        </Col>

        <Col col={4} xs={12} sm={12} md={4}>
          <div className='benefits__card'>
            <img src={benefits3} />
            <h2>{t('careerProPage.planPage.sections.benefits.list3.title')}</h2>
            <p>{t('careerProPage.planPage.sections.benefits.list3.des')}</p>
          </div>
        </Col>

        <Col col={4} xs={12} sm={12} md={4}>
          <div className='benefits__card'>
            <img src={benefits4} />
            <h2>{t('careerProPage.planPage.sections.benefits.list4.title')}</h2>
            <p>{t('careerProPage.planPage.sections.benefits.list4.des')}</p>
          </div>
        </Col>

        <Col col={4} xs={12} sm={12} md={4}>
          <div className='benefits__card'>
            <img src={benefits5} />
            <h2>{t('careerProPage.planPage.sections.benefits.list5.title')}</h2>
            <p>{t('careerProPage.planPage.sections.benefits.list5.des')}</p>
          </div>
        </Col>

        <Col col={4} xs={12} sm={12} md={4}>
          <div className='benefits__card'>
            <img src={benefits6} />
            <h2>{t('careerProPage.planPage.sections.benefits.list6.title')}</h2>
            <p>{t('careerProPage.planPage.sections.benefits.list6.des')}</p>
          </div>
        </Col>
      </Row>
    </Container>
  )
}

export default withTranslations(CareerProBenefits)



