import React, { useEffect } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import plan_bg from 'Shared/assets/images/illustrations/plan_bg.svg'
import Container from 'Shared/components/Container'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import './HeroSection.scss'
import mixpanel from 'mixpanel-browser'

const HeroSection = ({ t }) => {
  useEffect(() => {
    mixpanel.track('Page View - Career Pro')
  }, [])

  return (
    <section className='plans__section'>
      <Container>
        <div>
          <h2>{t('careerProPage.planPage.sections.HeroSection.heading')} <span>CareerPro</span></h2>
          <p>{parseHtmlWithoutSpan(t('careerProPage.planPage.sections.HeroSection.description'))}</p>
        </div>
        <img src={plan_bg} alt="Career Pro Background" />
      </Container>
    </section>
  );
}

export default withTranslations(HeroSection)



